html {
  font-size: 100%;
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  color: #ffffff;
  background-color: #1f1f1f;
  font-family: "Roboto", sans-serif;
}

.App {
  height: 100vh;
  overflow: auto;
}

input:focus {
  outline: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

a,
a:visited,
a:active {
  font-size: 1rem;
  font-weight: 300;
}

h1 {
  font-size: 3rem;
  font-weight: 400;
}

h2 {
  font-size: 2.1875rem;
  font-weight: 300;
}

h3 {
  font-size: 1.25rem;
  font-weight: 400;
}

p,
span,
button,
input {
  font-size: 1rem;
  font-weight: 200;
}

button {
  height: 2.5rem;
  width: auto;
  padding: 0 1.875rem;
  background-color: #80e897;
  margin-right: 1.375rem;
  border: none;
  border-radius: 3.125rem;
}

input,
label {
  background-color: transparent;
  color: #ffffff;
  border: 1px solid #c4c4c4;
  height: 2.5rem;
  width: 30rem;
  padding: 0.688rem 0.938rem;
  border-radius: 0.75rem;
}

input::placeholder {
  color: #ffffff50;
}

.navbar {
  display: flex;
  height: 3.4375rem;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 3.75rem;
  color: #1f1f1f;
}
.navbar h1 {
  font-size: 1.875rem;
  color: #FFD673;
}
.navbar input {
  height: 2.5rem;
  width: 30rem;
  background-color: #ffffff;
  color: #1f1f1f;
  padding: 0.688rem 0.938rem;
  border: none;
  border-radius: 0.75rem;
}
.navbar input::placeholder {
  color: #1f1f1f;
}
.navbar .login-btn-parent span {
  color: #80e897;
}
.navbar .login-btn-parent button {
  margin-left: 1.375rem;
}

main.landing-page {
  height: 100%;
}

section.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  padding: 0 3.75rem;
}
section.hero-section img.hero-image {
  height: 90%;
  width: 45%;
  margin-right: 3.75rem;
}
section.hero-section .hero-text {
  width: 50%;
  color: #ffffff;
}
section.hero-section .hero-text h1 {
  font-size: 3rem;
}
section.hero-section .hero-text span.emphasized {
  font-size: 3rem;
  font-weight: 400;
  color: #FFD673;
}
section.hero-section .hero-text p {
  margin: 3.75rem 0;
}
section.hero-section .hero-text .login-btn-parent span {
  color: #80e897;
}
section.hero-section .hero-text .login-btn-parent button {
  margin-right: 1.375rem;
}

section.become-contributor img.hero-image {
  margin-left: 3.75rem;
  height: 70%;
}

section.keep-reading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3.75rem;
  height: 100vh;
}
section.keep-reading h1 {
  font-size: 3.75rem;
}
section.keep-reading h1.emphasized {
  color: #FFD673;
}
section.keep-reading p {
  text-align: center;
  width: 65%;
}

.signup,
.login,
.reset-pass {
  height: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.signup h1,
.login h1,
.reset-pass h1 {
  color: #FFD673;
  margin-left: auto;
  margin-right: auto;
}
.signup form,
.login form,
.reset-pass form {
  margin-top: 3.75rem;
}
.signup input,
.login input,
.reset-pass input {
  display: block;
  margin-bottom: 0.9375rem;
}
.signup button,
.login button,
.reset-pass button {
  display: block;
  margin-top: 3.75rem;
  margin-left: auto;
  margin-right: auto;
}

main.upload {
  display: flex;
}
main.upload form.upload-form {
  width: 50%;
  height: 100vh;
  padding-left: 3.75rem;
  display: flex;
  justify-content: center;
  align-items: baseline;
  flex-direction: column;
}
main.upload form.upload-form input.gbooks-link,
main.upload form.upload-form label {
  margin-top: 0.9375rem;
  color: #ffffff;
  display: flex;
  align-items: center;
}
main.upload form.upload-form label {
  color: #ffffff50;
}
main.upload form.upload-form input[type=file] {
  display: none;
}
main.upload form.upload-form .buttons-container {
  display: flex;
  margin-top: 3.75rem;
}
main.upload form.upload-form .buttons-container button {
  display: block;
  margin-top: 0;
}
main.upload form.upload-form .buttons-container button.upload:disabled {
  background-color: #488956;
}
main.upload img.bookwall {
  width: 50%;
}

main.pdf-renderer {
  height: 100vh;
}
main.pdf-renderer .pdf-container {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 90vh;
  margin: 0 auto;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}
main.pdf-renderer .pdf-container::-webkit-scrollbar {
  display: none;
}
main.pdf-renderer canvas {
  margin: 0 auto;
}
main.pdf-renderer button {
  display: block;
  position: fixed;
  top: 100px;
}
main.pdf-renderer button.next-page {
  margin-left: 100px;
}

.book-basic-details {
  display: flex;
  position: relative;
  flex-direction: column;
  margin-right: 1.875rem;
  background-size: cover;
  background-repeat: no-repeat;
}
.book-basic-details img {
  height: 14rem;
  width: 10.5rem;
  border-radius: 0.75rem;
}
.book-basic-details span {
  margin-top: calc(0.9375rem - 5px);
  width: 60%;
  font-weight: 400;
}
.book-basic-details span:hover {
  color: #FFD673;
  cursor: pointer;
}

main.explore {
  padding-left: 3.75rem;
}
main.explore section {
  margin-top: 3.75rem;
}
main.explore .books-details-container {
  display: flex;
  margin-top: 1.875rem;
}
main.explore .authors-details-container {
  display: flex;
  margin-top: 1.875rem;
}

.author-basic-details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 1.875rem;
}
.author-basic-details img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-position: 0px 0px;
  object-fit: cover;
}
.author-basic-details span {
  margin-top: calc(1.875rem - 5px);
}